<template>
  <div>
    <fTitle>
      <template #title>Message 消息提示</template>
      <template #introduce>常用于主动操作后的反馈提示。与 Notification 的区别是后者更多用于系统级通知的被动提醒。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce>从顶部出现，3 秒后自动消失。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content"><el-button :plain="true" @click="open1">默认用法</el-button></div>
    </el-col>
    <el-col :span="10">
<div class="grid-content"><el-button :plain="true" @click="open2">VNode</el-button></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="5" v-for="item in typeList" :key="item.type">
<div class="grid-content">
  <el-button :plain="true" @click="open3(item)">{{ item.type }}</el-button>
</div>
    </el-col>
  </el-row>
  <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content">
  <el-button  :plain="true" @click="openHTML">使用 HTML 片段</el-button>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      typeList: [
        {
          text: '这是一条普通消息',
          type: 'info'
        },
        {
          text: '恭喜你，这是一条成功消息',
          type: 'success'
        },
        {
          text: '警告哦，这是一条警告消息',
          type: 'warning'
        },
        {
          text: '错了哦，这是一条错误消息',
          type: 'error'
        }

      ]
    }
  },
  methods: {
    open1 () {
      this.$message('这是一条消息提示')
    },
    open2 () {
      const h = this.$createElement
      // this.$createElement该语法将h定义为一个自定义的options
      // h('i', { style: 'color: teal' }, 'VNode')其三个参数分别为HTML符号,对应的样式,对应的内容
      this.$message({
        message: h('p', null, [
          h('span', null, '内容可以是 '),
          h('h4', { style: 'color: teal' }, 'VNode')
        ])
      })
    },
    open3 (item) {
      this.$message({
        showClose: true,
        message: item.text,
        type: item.type
      })
    },
    openHTML () {
      this.$message({
        dangerouslyUseHTMLString: true,
        message: '<h3>这是自定义HTML内容</h3><i style="font-size: 90px;" class="el-icon-eleme">'
      })
    }
  }

}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
